import Request from '../plugins/request/js/index';

export default {
    //  token
    getMockToken (id) {
		return Request().request({
		    url: '/music/getDetail',
		    method: 'post',
		    params: { id: id }
		});
    },

    //  tokenid 2460392754  successcode 200  fail code  401
    checkMockToken (token) {
        return Request().request({
            url: '/test/token/check',
            method: 'get',
            params: { name: 'xxx' },
            header: { token, uid: 'ux' },
            count: 0 //    
        });
    }
}