import log from './log';
import interfaces from './interfaces';
/**
 * 
 * @param {any} customId
 * @param {String} filePath
 * @param {Object} picsUpLoadData
 */
export function UpLoadFile(customId, filePath, picsUpLoadData) { // : (, , )
	log(':' + JSON.stringify(picsUpLoadData));
	if (filePath.substring(0, 4) === 'http') { //: ,  resolve. customId, 
		if (customId != 'imgBlob') {
			const replacePath = filePath.replace(interfaces.baseUrl, '');
			return Promise.resolve({
				data: replacePath
			});
		}
	}
	let _this = this;
	let url = '';
	let formData = {};
	let name = '';
	switch (customId) { //picsUpLoadFileType, urlformDataname
		case 'UpLoadImage_1': //
			url = '';
			formData = {};
			name = '';
			break;
		case 'imgBlob': // 
			url = interfaces.upLoadImg;
			formData = {};
			name = 'imgBlob'; //  name
			break;
		default: //
			url = interfaces.upLoadImg;
			formData = {};
			name = '';
			break;
	}
	if (!url) {
		uni.showToast({title:'url'});
		return new Promise((rs, rj) => {
			rj('url');
		});
	}
	if (!filePath) {
		uni.showToast({title:'filePath'});
		return new Promise((rs, rj) => {
			rj('filePath');
		});
	}
	return new Promise((reslove, reject) => {
		uni.showLoading({
			title: ''
		})
		const obj = {
			url,
			name,
			filePath,
			success(res) {
				log('UpLoadFilesuccess: ' + JSON.stringify(res));
				uni.hideLoading();
				reslove(res)
			},
			fail(err) {
				log('UpLoadFilefail')
				log(JSON.stringify(err))
				uni.hideLoading();
				reject();
			}
		}
		
		console.log(obj)
		








		obj.formData = formData;

		
		console.log(obj)
		uni.uploadFile(obj);
	})
}

/**
 * 
 * @param {String} vals
 * @param {String} val
 */
export function pics_splice(vals, val) { // , vals val
	console.log('xxxxxx')
	console.log(vals)
	if (typeof(vals) !== 'string') { // 
		vals = val || '|'; // 
		return vals;
	}
	vals += val ? '|' + val : '|';
	return vals; // return vals
}

function renderUrlData(u, d) {
	let c = '';
	let n = 0;
	if (d)
		for (let i in d) {
			if (n > 0)
				c += '&' + i + '=' + d[i];
			else
				c += '?' + i + '=' + d[i];
			++n;
		}
	return u + c;
}